import React, { useState, useEffect } from "react"
import { connect } from "react-redux"
import socket from "../transport/Connect"
import HeadDialog from "./HeadDialog"
import Dialog from "./Dialog"
import selectors from "../selectors"
import { openDialog } from "../actions/dialogs"

function DialogContainer(props) {
  // const [messages, setMessages] = useState([]);
  const [dialog, setDialog] = useState(null)

  useEffect(() => {
    props.dialogs.forEach(dialog => {
      if (dialog.active) {
        setDialog(dialog)
      }
    })
  }, [props.dialogs])

  useEffect(() => {
    // if (props.messages) {
    //    props.messages.map(message => {
    //       if (message.ConversationId === dialogId) {
    //          // console.log(messages);
    //          let mess = messages;
    //          mess.push(message);
    //          // console.log(mess);
    //          setMessages(messages.push(message));
    //       }
    //    });
    // }
  }, [props.messages])

  useEffect(() => {
    if (dialog) {
      // console.log(dialog)
      // console.log(dialog.messagesRecieved)

      // если для этого диалога сообщения еще не запрашивались, то запрашиваем
      if (!dialog.messagesRecieved) {
        // запрос на получение сообщений
        const data = {
          ConversationType: dialog.type === "channel" ? 3 : dialog.ConversationType,
          ConversationId: dialog.ConversationId ? dialog.ConversationId : dialog.ChannelId,
          NavigationMessageTime: null,
          MessageId: null,
          RequestType: 25,
          RequestId: socket.makeId(),
          Type: 0
        }

        socket.send(data)
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [dialog])

  return (
    <div className="dialog-container">
      <HeadDialog {...props} />
      {selectors.getActiveDialog(props.dialogs) ? <Dialog dialog={dialog} {...props} /> : null}
    </div>
  )
}

function mapStateToProps(state) {
  return {
    dialogs: state.dialogs,
    messages: state.messages
  }
}

const mapDispatchToProps = dispatch => {
  return {
    openDialog: dialogId => dispatch(openDialog(dialogId))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(DialogContainer)
