/* eslint-disable */
export function dialogs(state = [], action) {
   if (action.type === 'DIALOGS_RECEIVED') {
      let newState = [...state]
      let activeDialog = newState.find(dialog => dialog.active)
      newState = action.dialogs
      if (activeDialog) {
         newState.forEach(dialog => {
            if (dialog.ConversationId === activeDialog.ConversationId) {
               dialog.active = true
               dialog.messagesRecieved = activeDialog.messagesRecieved
            }
         })
      }
      return newState
   }
   else if(action.type === 'OPEN_DIALOG') {
      let newState = [...state]
      // В action.payload.data может содержаться и сам диалог и объект юзера диалога с которым еще нет
      // Если диалога с юзером нет, то создаем фэйковый диалог
      // И после отправи сообщения диалог будет создан и просто запрашиваем его на сервере
      if (action.payload.type === 'dialog') {
         newState.forEach(function(dialog){
            dialog.active = false
   
            if (dialog.ConversationId === action.payload.data.ConversationId) {
               dialog.active = true
               dialog.UnreadedCount = 0
               dialog.type = action.payload.type
            }
         })
      } else if (action.payload.type === 'user') {
         let dialogWithUser = newState.find(dialog => dialog.SecondUid === action.payload.data.Id)
         if (dialogWithUser) {
            newState.forEach(dialog => {
               dialog.active = false

               if (dialog.SecondUid === action.payload.data.Id) {
                  dialog.active = true
                  dialog.UnreadedCount = 0
                  dialog.type = 'dialog'
               }
            })
         } else {
            let fakeDialog = {}
         
            fakeDialog.ConversationId = action.payload.data.Id,
            fakeDialog.Title = action.payload.data.NameFirst + ' ' + action.payload.data.NameSecond,
            fakeDialog.Photo = action.payload.data.Photo,
            fakeDialog.ConversationType = 1,
            fakeDialog.SecondUid = action.payload.data.Id,
            fakeDialog.fake = true
   
            newState.push(fakeDialog)
   
            newState.forEach(function(dialog){
               dialog.active = false
      
               if (dialog.ConversationId === action.payload.data.Id) {
                  dialog.active = true
                  dialog.UnreadedCount = 0
                  dialog.type = action.payload.type
               }
            })
         }
      } else if (action.payload.type === 'channel') {
         newState.push(action.payload.data)

         newState.forEach(function(dialog){
            dialog.active = false
   
            if (dialog.ConversationId === action.payload.data.Id) {
               dialog.active = true
               dialog.UnreadedCount = 0
               dialog.type = action.payload.type,
               dialog.fake = true
            }
         })
      } else if (action.payload.type === 'chat') {
         let chat = newState.find(dialog => dialog.ConversationId === action.payload.data.Id)
         if (chat) {
            newState.forEach(dialog => {
               dialog.active = false

               if (dialog.ConversationId === action.payload.data.Id) {
                  dialog.active = true
                  dialog.UnreadedCount = 0
                  dialog.type = 'chat'
               }
            })
         } else {
            let fakeDialog = {}
         
            fakeDialog.ConversationId = action.payload.data.Id,
            fakeDialog.Title = action.payload.data.NameFirst + ' ' + action.payload.data.NameSecond,
            fakeDialog.Photo = action.payload.data.Photo,
            fakeDialog.ConversationType = 1,
            fakeDialog.SecondUid = action.payload.data.Id,
            fakeDialog.fake = true
   
            newState.push(fakeDialog)
   
            newState.forEach(function(dialog){
               dialog.active = false
      
               if (dialog.ConversationId === action.payload.data.Id) {
                  dialog.active = true
                  dialog.UnreadedCount = 0
                  dialog.type = action.payload.type
               }
            })
         }
      }

      return newState
   }
   else if(action.type === 'CLOSE_DIALOG') {
      console.log(action)
      let newState = [...state]

      newState.forEach(dialog => {
         if (dialog.active) {
            dialog.active = false
         }
      })

      return newState
   }
   else if(action.type === 'MESSAGES_RECEIVED'){
      let newState = [...state]

      if (action.messages.length) {
         let conversationId = action.messages[0].ConversationId
         newState.forEach(dialog => {
            if (dialog.ConversationId === conversationId) {
               // устанавливаем флаг по которому при открытии диалога проверяем запрашивались ли для него сообщения
               dialog.messagesRecieved = true
               // превью диалога отображается в сайдбаре
               dialog.PreviewText = action.messages[0].Text
               // если диалог не открыт, то накручиваем счетчик непрочитаных
               if(!dialog.active) {
                  dialog.UnreadedCount += 1
               }
               // поднимаем диалог на первое место в сайдбаре
               // let dialogIndex = newState.indexOf(dialog)
               // newState.splice(dialogIndex, 1)
               // newState.unshift(dialog)
            }
         })
      }

      return newState
   }
   else {
      return state
   }
}
