/* eslint-disable */
const defaultState = {
  profile: null,
  search: false,
  showDrawer: false,
  modals: {
    createChatModal: false,
    contactsModal: false,
    contactsGroupsModal: false,
    settingsModal: false
  }
}

export function ui(state = defaultState, action) {
  if(action.type === 'OPEN_PROFILE') {
    let newState = {...state}
    newState.profile = action.dialog
    return newState
  }
  else if (action.type === 'SEARCH_START') {
    let newState = {...state}
    newState.search = action.text
    return newState
  }
  else if (action.type === 'SEARCH_END') {
    let newState = {...state}
    newState.search = false
    return newState
  }
  else if (action.type === 'TOGGLE_DRAWER') {
    let newState = {...state}   
    newState.showDrawer = !newState.showDrawer
    return newState
  }
  else if (action.type === 'TOGGLE_MODAL') {
    let newState = {...state}
    if (action.name === "close") {
      for (var name in newState.modals) {
        newState.modals[name] = false
    }
    } else {
      newState.modals[action.name] = !newState.modals[action.name]
    }
    return newState
  }
  else {
    return state
  }
}
