/* eslint-disable */
// В chats хрнаится объект только одного чата, информацию о котором просматривается в данный момент
export function chats(state = [], action) {
if (action.type === 'CHATS_RECEIVED') {
    let newState = [...state];
    newState = action.chats;
    return newState;
  }
  else if (action.type === 'CHATS_USERS_RECEIVED') {
    let newState = [...state];
    if (newState[0].Id === action.chatsUsers[0].ChatId) {
      newState[0].Users = action.chatsUsers;
    }
    return newState;
  }
  else {
    return state;
  }
}
