import React, { Component } from "react"
import { Row, Col, Button, Menu, Dropdown, Icon } from "antd"
import { Link } from "react-router-dom"
import { connect } from "react-redux"
import socket from "../transport/Connect"
import { setNode } from "../actions/node"

class Auth extends Component {
  constructor() {
    super()
    this.state = {
      nodes: [],
      activeNode: null
    }
  }

  componentDidMount() {
    this.setState({
      nodes: JSON.parse(localStorage.getItem("nodes")),
      activeNode: JSON.parse(localStorage.getItem("node"))
    })
  }

  selectNode(node) {
    this.setState({
      activeNode: node.item.props.data
    })
    localStorage.setItem("node", JSON.stringify(node.item.props.data))
  }

  connectNode() {
    console.log('connect', this.state.activeNode)
    socket.start(this.state.activeNode.Domains[0])
  }

  render() {
    const menu = (
      <Menu onClick={this.selectNode.bind(this)}>
        {
          this.state.nodes ?
            this.state.nodes.map((node, i) => {
              return (
                <Menu.Item key={i} data={node}>
                  {node.Name}
                </Menu.Item>
              )
            })
          : null
        }
      </Menu>
    )
    return (
      <Row
        type="flex"
        align="middle"
        justify="center"
        style={{ height: "100vh" }}
      >
        <Col>
          <div className="auth__type">
            <h1>Вход</h1>
            <h2>Выберите способ входа</h2>
            <Link to={this.props.match.url + "/by-phone"} onClick={this.connectNode.bind(this)}>
              <Button type="secondary">По номеру телефона</Button>
            </Link>
            <br />
            <Link to={this.props.match.url + "/by-email"} onClick={this.connectNode.bind(this)}>
              <Button type="secondary">По электронной почте</Button>
            </Link>
            <br />
            <Button type="secondary">Через приложение</Button>
            <br />
            <br />
            <Link to={"/registration"} onClick={this.connectNode.bind(this)}>Регистрация</Link>
            <br />
            <div className="auth__type_server">
              <span>Сервер: </span>
              <Dropdown overlay={menu}>
                <span className="ant-dropdown-link">
                  {localStorage.getItem("node")
                    ? JSON.parse(localStorage.getItem("node")).Name
                    : "Автоматически"}
                  <Icon type="down" />
                </span>
              </Dropdown>
            </div>
          </div>
        </Col>
      </Row>
    )
  }
}

function mapStateToProps(state) {
  return {}
}

const mapDispatchToProps = dispatch => {
  return {
    setNode: node => dispatch(setNode(node))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Auth)
