import React, {useState, useEffect} from "react"
import { connect } from "react-redux"
import { Modal, Tabs } from "antd"
import { toggleModal } from "../actions/ui"
import Request from "../transport/Request"
import Avatar from "./Avatar"
import { openDialog } from "../actions/dialogs"

function ContactsModal(props) {
  const [contacts, setContacts] = useState([])

  const { TabPane } = Tabs

  useEffect(() => {
    Request.getUserContacts()
    Request.getUserGroups()
  }, [])

  useEffect(() => {
    const contactsTmp = []
    props.users.forEach(user => {
      if (props.contactsIDs.find(id => user.Id === id)) {
        contactsTmp.push(user)
      }
    })
    setContacts(contactsTmp)
  }, [props.users, props.contactsIDs])

  const handleOpenChat = user => {
    let payload = {}
    payload.data = user
    payload.type = "user"
    props.openDialog(payload)
    props.toggleModal("close")
  }

  const changeGroup = groupId => {
    if (groupId === "all") {
      Request.getUserContacts()
    } else {
      Request.getGroupContacts(groupId)
    }
  }

  const showContacts = () => {
    return contacts.map(contact => {
      return (
        <div className="contact" key={contact.Id} onClick={() => handleOpenChat(contact)}>
          <Avatar user={contact} />
          <div className="name">
            {`${contact.NameFirst} ${contact.NameSecond}`}
          </div>
        </div>
      )
    })
  }

  const showGroups = () => {
    return props.groups.map(group => {
      return (
        <TabPane tab={group.Title} key={group.GroupId}>
          {showContacts()}
        </TabPane>
      )
    })
  }

  return (
    <Modal
      visible={props.contactsModal}
      onCancel={() => props.toggleModal("contactsModal")}
      title="Контакты"
      footer={null}
      maskClosable
      className="contacts"
    >
      <Tabs defaultActiveKey="1" onChange={changeGroup}>
        <TabPane tab="Все" key="all">
          {showContacts()}
        </TabPane>
        {showGroups()}
      </Tabs>
    </Modal>
  )
}

function mapStateToProps(state) {
  return {
    contactsModal: state.ui.modals.contactsModal,
    users: state.users,
    contactsIDs: state.contacts.usersIDs,
    groups: state.contacts.groups
  }
}

const mapDispatchToProps = dispatch => {
  return {
    toggleModal: name => dispatch(toggleModal(name)),
    openDialog: data => dispatch(openDialog(data))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(ContactsModal)
