// eslint-disable-next-line
export function getFormatedUser(data) {
  const users = []
  data.forEach(user => {
    const tempUser = {
      About: user.About,
      Birthday: user.Birthday,
      BlackList: user.BlackList,
      City: user.City,
      Confirmed: user.Confirmed,
      Contact: user.Contact,
      ContactsPrivacy: user.ContactsPrivacy,
      Country: user.Country,
      Emails: user.Emails,
      Encryption: user.Encryption,
      Groups: user.Groups,
      Id: user.Id,
      Keys: user.Keys,
      Language: user.Language,
      NameFirst: user.NameFirst,
      NameSecond: user.NameSecond,
      NodeId: user.NodeId,
      Online: user.Online,
      Phones: user.Phones,
      Photo: user.Photo,
      RegistrationDate: user.RegistrationDate,
      Security: user.Security,
      Tag: user.Tag,
      Visible: user.Visible
    }
    users.push(tempUser)
  })

  return users
}

export function getFormatedChatsUsers(data) {
  const users = []
  data.forEach(user => {
    const tempUser = {
      Banned: user.Banned,
      ChatId: user.ChatId,
      Deleted: user.Deleted,
      InviterId: user.InviterId,
      IsMuted: user.IsMuted,
      Joined: user.Joined,
      UserId: user.UserId,
      UserInfo: user.UserInfo,
      UserRole: user.UserRole
    }
    users.push(tempUser)
  })

  return users
}

export function getUserById(id, users) {
  const foundUser = users.find(user => user.Id === id)
  return foundUser
}

export function getUserInitials(dialog) {
  if (!dialog.Photo) {
    let initials = ""

    if (dialog.Title || dialog.ChannelName) {
      let defaultAva = dialog.Title || dialog.ChannelName
      defaultAva = defaultAva.split(" ", 2)

      if (defaultAva[0]) {
        initials = defaultAva[0][0]
      }
      if (defaultAva[1]) {
        initials += defaultAva[1][0]
      }
    } else if (dialog.NameFirst) {
      initials = dialog.NameFirst[0]
      if (dialog.NameSecond) {
        initials = initials + dialog.NameSecond[0]
      }
    } else {
      const secondUid = dialog.SecondUid + ''
      initials = secondUid.substr(0, 2)
    }

    return initials
  }
}
