/* eslint-disable */
import React, { useState } from "react"
import { connect } from "react-redux"
import { BrowserRouter as Router, Route } from "react-router-dom"
import Sidebar from "./Sidebar"
import selectors from "../selectors"
import DialogContainer from "./DialogContainer"
import ProfileModal from "./ProfileModal"
import CreateChatModal from "./CreateChatModal"
import ContactsModal from "./ContactsModal"
import ContactsGroupsModal from "./ContactsGroupsModal"
import { toggleDrawer, toggleModal } from "../actions/ui"
import { Drawer } from 'antd';
import SettingsModal from "./SettingsModal"

function MainContainer(props) {
  const [windowWidth, setWindowWidth] = useState(document.body.clientWidth)

  window.addEventListener("resize", () => {
    setWindowWidth(document.body.clientWidth)
  }, false)

  return (
    <Router>
      <div className="page">
        <div className="page__content">
          <Drawer
            visible={props.ui.showDrawer}
            // visible={true}
            placement='left'
            closable={true}
            onClose={() => props.toggleDrawer()}
            width='366'
          >
            {
              props.user ? (
                <div className="drawer-user">
                  <div className="drawer-user__ava">
                    { props.user.Photo ? (
                      <div
                        style={{
                          width: "100%",
                          height: "100%",
                          backgroundSize: "cover",
                          backgroundPosition: "center center",
                          backgroundImage: `url(https://${props.node}:5000/api/Files/${props.user.Photo})`,
                          backgroundRepeat: "no-repeat"
                        }}
                      />
                    ) : (
                      <div className="drawer-user__photo-default avatar-default">{defaultAva}</div>
                    )}
                  </div>
                  <div className="drawer-user__name">
                    {props.user.NameFirst + ' ' + props.user.NameSecond}
                  </div>
                </div>
              ) : null
            }
            <ul className="menu">
              <li onClick={() => props.toggleModal("createChatModal")}>Создать групповой чат</li>
              <li onClick={() => props.toggleModal("contactsModal")}>Контакты</li>
              <li onClick={() => props.toggleModal("contactsGroupsModal")}>Группы контактов</li>
              <li onClick={() => props.toggleModal("settingsModal")}>Настройки</li>
            </ul>
          </Drawer>
          {windowWidth > 675 ? (
            <>
              <Sidebar {...props} />
              <DialogContainer />
            </>
          ) : (
            !selectors.getActiveDialog(props.dialogs) ? <Sidebar {...props} /> : <DialogContainer />
          )}
        </div>
      </div>
      <ProfileModal />
      <CreateChatModal />
      <ContactsModal />
      <ContactsGroupsModal />
      <SettingsModal />
    </Router>
  )
}

function mapStateToProps(state) {
  return {
    dialogs: state.dialogs,
    ui: state.ui,
    user: state.users.find(user => user.currentUser === true),
    node: state.node.Domains[0]
  }
}

const mapDispatchToProps = dispatch => {
  return {
    toggleDrawer: () => dispatch(toggleDrawer()),
    toggleModal: name => dispatch(toggleModal(name))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(MainContainer)
