/* eslint-disable */
import React, { useState, useEffect } from "react"
import { connect } from "react-redux"
import { NavLink } from "react-router-dom"
import { Modal } from "antd"
import { openProfile } from "../actions/ui"
import { openDialog } from "../actions/dialogs"
import socket from "../transport/Connect"
import selectors from "../selectors"

function ProfileModal(props) {
  const [visible, setVisible] = useState(false)
  const [profile, setProfile] = useState(null)
  const [prevProfile, setPrevProfile] = useState(null)

  useEffect(() => {
    if(props.profile) {
      if(props.profile.ConversationType === 1){
        const user = selectors.getUserById(props.profile.SecondUid, props.users)
        if (user) {
          setProfile(user)
          setVisible(true)
        }
      } else if (props.profile.NameFirst) {
        setProfile(props.profile)
        setVisible(true)
      } else if (props.profile.Users) {
        setProfile(props.profile)
        setVisible(true)
      } else if (props.profile.ConversationType === 2){
        const getChatInfo = {
          RequestType: 45,
          ChatsId: [props.profile.ConversationId],
          RequestId: socket.makeId(),
          Type: 0
        }
    
        socket.send(getChatInfo)
      }
    }
  }, [props.profile])

  useEffect(() => {
    if (props.chats.length) {
      setProfile(props.chats[0])
      setVisible(true)

      // запрашиваем недостающих юзеров для отображения в профиле
      const undefinedUsers = []
      props.chats[0].Users.map(user => {
        const foundUser = selectors.getUserById(user.UserId, props.users)
        if(!foundUser){
          undefinedUsers.push(user.UserId)
        }
      })
      if(undefinedUsers.length){
        const requestUsers = {
          UsersId: [...undefinedUsers],
          RequestType: 28,
          RequestId: socket.makeId(),
          Type: 0
        }

        socket.send(requestUsers)
      }
    }
  }, [props.chats])

  const openProfile = data => {
    setPrevProfile(profile)
    props.openProfile(data)
  }

  const showUsers = () => {
    return props.chats[0].Users.map(user => {
      const foundUser = selectors.getUserById(user.UserId, props.users)
      if(foundUser){
        let name = null
        if (foundUser.NameFirst) {
          name = foundUser.NameFirst
        }
        if (foundUser.NameSecond) {
          name = name + ' ' + foundUser.NameSecond
        }
        return (
          <div key={foundUser.Id} className='profile__info_user' onClick={() => {openProfile(foundUser)}}>
            {
              foundUser.Photo ? 
                <div
                  style={{
                    width: "50px",
                    height: "50px",
                    backgroundSize: "cover",
                    backgroundPosition: "center center",
                    backgroundImage: `url(https://${props.node}:5000/api/Files/${foundUser.Photo})`,
                    backgroundRepeat: "no-repeat",
                    borderRadius: 100,
                    margin: '0 auto',
                    marginTop: "10px"
                  }}
                ></div>
              : 
              <div className='profile__info_ava'>{selectors.getUserInitials(foundUser)}</div> 
            }
            {name ? name : 'Имя скрыто'}
          </div>
        )
      }
    })
  }

  const handleCancelProfile = () => {
    setVisible(false)
    props.openProfile(null)
  }

  const handleOpenDialog = () => {
    let payload = {}
    payload.data = props.profile
    if (props.profile.type === "dialog") {
      payload.type = "dialog"
    } else {
      payload.type = "user"
    }
    props.openDialog(payload)
    handleCancelProfile()
  }

  const handleBackProfile = () => {
    props.openProfile(prevProfile)
    setPrevProfile(null)
  }

  return (
    <Modal
      visible={visible}
      onCancel={handleCancelProfile}
      footer={null}
      maskClosable
    >
      {
        profile ? 
          <div className='profile'>
            {
              prevProfile ? <div onClick={handleBackProfile}>Назад</div> : null
            }
            {
              profile.Photo ? 
                <div
                  className='profile__ava'
                  style={{
                    width: "100px",
                    height: "100px",
                    backgroundSize: "cover",
                    backgroundPosition: "center center",
                    backgroundImage: `url(https://${props.node}:5000/api/Files/${profile.Photo})`,
                    backgroundRepeat: "no-repeat",
                    borderRadius: 100,
                    margin: '0 auto'
                  }}
                ></div>
              : null 
            }
            {
              profile.Name ?
                <div className='profile__name'>{profile.Name}</div>
              :
                <div className='profile__name'>
                  {profile.NameFirst ? profile.NameFirst  : null}
                  {' '}
                  {profile.NameSecond ? profile.NameSecond : null}
                </div>
            }
            {
              profile.Phones ? 
                profile.Phones.length > 0 ?
                  <div className='profile__info'>
                    <div className='profile__info_name'>Телефон</div>
                    <div className='profile__info_value'>{profile.Phones[0].FullNumber}</div>
                  </div>
                : null
              : null
            }
            {
              profile.Emails ? 
                <div className='profile__info'>
                  <div className='profile__info_name'>Email</div>
                  <div className='profile__info_value'><a href={`mailto:${profile.Emails[0]}`}>{profile.Emails[0]}</a></div>
                </div>
              : null
            }
            {
              profile.About ? 
                <div className='profile__info'>
                  <div className='profile__info_name'>О себе</div>
                  <div className='profile__info_value'>{profile.About}</div>
                </div>
              : null
            }
            {
              profile.Users ? 
                <div className='profile__info'>
                  <div className='profile__info_name'>Участники</div>
                  <div className='profile__info_users'>{showUsers()}</div>
                </div>
              : null
            }
            {
              props.profile ? 
                <NavLink
                  to={"/"}
                  onClick={handleOpenDialog}
                >
                  <div className='profile__open_chat'>Открыть чат</div>
                </NavLink>
              : null
            }
          </div>
        : null
      }
      
    </Modal>
  )
}

function mapStateToProps(state) {
  return {
    profile: state.ui.profile,
    users: state.users,
    chats: state.chats,
    node: state.node.Domains[0]
  }
}

const mapDispatchToProps = dispatch => {
  return {
    openProfile: user => dispatch(openProfile(user)),
    openDialog: dialogId => dispatch(openDialog(dialogId))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(ProfileModal)
