// eslint-disable-next-line
export function getFormatedDialogs(data) {
  const dialogs = []
  data.forEach(dialog => {
    const tempDialogs = {
      AttachmentType: dialog.AttachmentType,
      ConversationId: dialog.ConversationId,
      ConversationType: dialog.ConversationType,
      IsMuted: dialog.IsMuted,
      LastMessageId: dialog.LastMessageId,
      LastMessageSenderId: dialog.LastMessageSenderId,
      LastMessageSenderName: dialog.LastMessageSenderName,
      LastMessageTime: dialog.LastMessageTime,
      Photo: dialog.Photo,
      PreviewText: dialog.PreviewText,
      Read: dialog.Read,
      SecondUid: dialog.SecondUid,
      Title: dialog.Title,
      UnreadedCount: dialog.UnreadedCount
    }
    dialogs.push(tempDialogs)
  })

  return dialogs
}

export function getFormatedChats(data) {
  const chats = []
  data.forEach(chat => {
    const tempChat = {
      About: chat.About,
      Id: chat.Id,
      Key: chat.Key,
      Name: chat.Name,
      NodesId: chat.NodesId,
      Photo: chat.Photo,
      Public: chat.Public,
      Security: chat.Security,
      Tag: chat.Tag,
      Type: chat.Type,
      Users: chat.Users,
      Visible: chat.Visible
    }
    chats.push(tempChat)
  })

  return chats
}

export function getActiveDialog(dialogs) {
  const dialog = dialogs.find(dialog => dialog.active)

  return dialog
}
