import React, { useState, useEffect } from "react"
import { Row, Col, Button, Form, Input, Dropdown, Menu, Icon } from "antd"
// import {BrowserRouter as Router, Redirect} from "react-router-dom";
import socket from "../transport/Connect"

function Registration() {
  const [name, setName] = useState("")
  const [surname, setSurname] = useState("")
  const [phone, setPhone] = useState("")
  const [email, setEmail] = useState("")
  const [nameErr, setNameErr] = useState(false)
  const [contactsErr, setContactsErr] = useState(false)
  const [btnLoader, setbtnLoader] = useState(false)
  const [activeNode, setActiveNode] = useState(null)

  useEffect(() => {
    socket.onMessage = data => {
      if (data.ErrorCode !== 0) {
        // console.log(data)
      } else if (data.ResponseType === 6) {
        localStorage.getItem("FileAccessToken", data.FileAccessToken)
        localStorage.getItem("AccessToken", data.Token.AccessToken)
        localStorage.getItem("RefreshToken", data.Token.RefreshToken)
        localStorage.getItem("User", data.User)

        window.location.replace("/")
      }
    }

    setActiveNode(JSON.parse(localStorage.getItem("node")))
  }, [])

  const handleRegistration = () => {
    if (name.length === 0) {
      setNameErr("Это поле обязательно для заполнения")
    } else if (phone.length === 0 && email.length === 0) {
      setContactsErr("Одно из этих полей обязательно для заполнения")
    } else {
      setbtnLoader(true)

      const regData = {
        User: {
          Id: 0,
          Phones: phone ? [phone] : null,
          NameFirst: name,
          NameSecond: surname,
          About: "",
          Photo: "",
          Country: "",
          City: "",
          Birthday: "",
          Language: null,
          NodeId: activeNode.Id,
          Online: null,
          Emails: email ? [email] : null,
          Blacklist: null,
          Visible: null,
          Security: null,
          Tag: 0,
          RegistrationDate: null
        },
        RequestId: socket.makeId(),
        RequestType: 37,
        Type: 0
      }

      socket.send(regData)
    }
  }

  const handleSetName = e => {
    setName(e.target.value)
    setNameErr(false)
  }

  const handleSetSurname = e => {
    setSurname(e.target.value)
  }

  const handleSetPhone = e => {
    setPhone(e.target.value)
    setContactsErr(false)
  }

  const handleSetEmail = e => {
    setEmail(e.target.value)
    setContactsErr(false)
  }

  const selectNode = data => {
    if (data.item.props.data) {
      setActiveNode(data.item.props.data)
      localStorage.setItem("node", JSON.stringify(data.item.props.data))
    }
  }

  const menu = (
    <Menu onClick={(node) => selectNode(node)}>
      {JSON.parse(localStorage.getItem("nodes")).map((node, i) => {
        return (
          <Menu.Item key={i} data={node}>
            {node.Name}
          </Menu.Item>
        )
      })}
      {
        // this.state.activeNode ? <Menu.Item key='auto' data={null}>Автоматически</Menu.Item> : null
      }
    </Menu>
  )

  return (
    <Row
      type="flex"
      align="middle"
      justify="center"
      style={{ height: "100vh" }}
    >
      <Col>
        <div className="auth__type">
          <h1>Регистрация</h1>
          <div>
            <Form className="login-form">
              <Form.Item
                validateStatus={nameErr ? "error" : ""}
                help={nameErr || ""}
              >
                <Input placeholder="Имя" onChange={handleSetName} />
              </Form.Item>
              <Form.Item validateStatus="">
                <Input placeholder="Фамилия" onChange={handleSetSurname} />
              </Form.Item>
              <Form.Item
                validateStatus={contactsErr ? "error" : ""}
                help={contactsErr || ""}
              >
                <Input placeholder="Номер телефона" onChange={handleSetPhone} />
              </Form.Item>
              <Form.Item
                validateStatus={contactsErr ? "error" : ""}
                help={contactsErr || ""}
              >
                <Input placeholder="Email" onChange={handleSetEmail} />
              </Form.Item>
              <Form.Item>
                <Row>
                  <Col style={{ textAlign: "center" }}>
                    <Button
                      type="primary"
                      onClick={handleRegistration}
                      loading={btnLoader}
                    >
                      Зарегистрироваться
                    </Button>
                  </Col>
                </Row>
              </Form.Item>
            </Form>
            <div className="auth__type_server">
              <span>Сервер: </span>
              <Dropdown overlay={menu}>
                <span className="ant-dropdown-link">
                  {activeNode
                    ? activeNode.Name
                    : "Автоматически"}
                  <Icon type="down" />
                </span>
              </Dropdown>
            </div>
          </div>
        </div>
      </Col>
    </Row>
  )
}

export default Registration
