/* eslint-disable */
import React, { useState, useEffect } from "react"
import { connect } from "react-redux"
import ReactTimeAgo from "react-time-ago"
import JavascriptTimeAgo from "javascript-time-ago"
import ru from "javascript-time-ago/locale/ru"
import { Icon } from "antd"
import socket from "../transport/Connect"
import { openProfile } from "../actions/ui"
import { closeDialog } from "../actions/dialogs"
import selectors from "../selectors"

JavascriptTimeAgo.locale(ru)

function HeadDialog(props) {
  const [dialog, setDialog] = useState(null)
  const [defaultAva, setDefaultAva] = useState("")
  const [user, setUser] = useState(null)
  const [windowWidth, setWindowWidth] = useState(document.body.clientWidth)

  useEffect(() => {}, [windowWidth])

  window.onresize = () => {
    setWindowWidth(document.body.clientWidth)
  }

  useEffect(() => {
    if (props.users && dialog) {
      const user = props.users.find(user => user.Id === dialog.SecondUid)
      setUser(user)
    }
  }, [props.users, dialog])

  useEffect(() => {
    props.dialogs.forEach(dialog => {
      if (dialog.active) {
        setDialog(dialog)
      }
    })
  }, [props.dialogs])

  useEffect(() => {
    if (dialog) {
      // добавляем инициалы в стэйт если нет аватарки
      if (!dialog.Photo) {
        setDefaultAva(selectors.getUserInitials(dialog))
      }
    }
  }, [dialog])

  const logout = () => {
    const logout = {
      Full: false,
      AccessToken: localStorage.getItem("AccessToken"),
      RequestType: 34,
      RequestId: socket.makeId(),
      Type: 0
    }

    socket.send(logout)

    localStorage.removeItem("AccessToken")
    localStorage.removeItem("FileAccessToken")
    localStorage.removeItem("RefreshToken")
    localStorage.removeItem("User")

    window.location.replace("/")
  }

  const handleOpenProfile = () => {
    props.openProfile(dialog)
  }

  return (
    <div className="head-dialog">
      {windowWidth <= 675 && selectors.getActiveDialog(props.dialogs) ? (
        <div className="head-dialog__back" onClick={props.closeDialog} >
          <Icon type="left" />
        </div>
      ) : null}
      <div className="head-dialog__user" onClick={handleOpenProfile}>
        <div className="head-dialog__ava">
          {dialog ? (
            dialog.Photo ? (
              <div
                style={{
                  width: "100%",
                  height: "100%",
                  backgroundSize: "cover",
                  backgroundPosition: "center center",
                  backgroundImage: `url(https://${props.node}:5000/api/Files/${dialog.Photo})`,
                  backgroundRepeat: "no-repeat"
                }}
              />
            ) : (
              <div className="head-dialog__photo-default avatar-default">{defaultAva}</div>
            )
          ) : null}
        </div>
        <div>
          <div className="head-dialog__name">{dialog ? dialog.Title || dialog.ChannelName || 'Имя скрыто' : null}</div>
          <div className="head-dialog__status">
            {user ?
              user.Online ? 
                <ReactTimeAgo date={user.Online * 1000} locale="ru" />
              : null
            : null}
          </div>
        </div>
      </div>
      <div onClick={logout} className="head-dialog__logout">
        Выход
      </div>
      {/* <div className='head__lock'>
        <LockIcon />
      </div> */}
    </div>
  )
}

function mapStateToProps(state) {
  return {
    dialogs: state.dialogs,
    users: state.users,
    node: state.node.Domains[0]
  }
}

const mapDispatchToProps = dispatch => {
  return {
    openProfile: data => dispatch(openProfile(data)),
    closeDialog: () => dispatch(closeDialog())
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(HeadDialog)
