import React from "react"
import { connect } from "react-redux"
import { searchStart, searchEnd } from "../actions/search"
import socket from "../transport/Connect"
import { Input } from 'antd'

const { Search } = Input

function SearchComponent(props) {
  const searchDialogs = value => {
    let text = ""
    // в функцию может прилетать и текст и евент
    if (typeof value === "object") {
      text = value.target.value
    } else if (typeof value === "string") {
      text = value
    }

    if (text.length) {
      props.searchStart(text.toLowerCase())
    } else if (!text.length) {
      props.searchEnd()
    }

    const searchRequest = {
      RequestType: 46,
      SearchQuery: text,
      RequestId: socket.makeId(),
      Direction: true,
      Type: 0
    }

    socket.send(searchRequest)
  }

  return (
    <div className="search">
      <Search
        placeholder="Поиск"
        onSearch={searchDialogs}
        onChange={searchDialogs}
        enterButton
      />
    </div>
  )
}

function mapStateToProps(state) {
  return {
    search: state.ui.search
  }
}

const mapDispatchToProps = dispatch => {
  return {
    searchStart: data => dispatch(searchStart(data)),
    searchEnd: () => dispatch(searchEnd())
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(SearchComponent)
