/* eslint-disable */
import React, {useState, useEffect} from "react"
import { connect } from "react-redux"
import { Modal, Form, Input, Switch, Divider, Upload, Icon, message, Button } from "antd"
import { toggleModal } from "../actions/ui"
import Request from "../transport/Request"
import axios from 'axios'

function CreateChatModal(props) {
  const [chatName, setChatName] = useState(null)
  const [nameError, setNameError] = useState(false)
  const [chatPrivate, setChatPrivate] = useState(false)
  const [chatChannel, setChatChannel] = useState(false)
  const [loading, setLoading] = useState(false)
  const [imageUrl, setImageUrl] = useState(null)

  const handleSetChatName = value => {
    setChatName(value)
    setNameError(false)
    if (value.length < 1) {
      setNameError(true)
    }
  }

  const handleCreateChat = () => {
    if (!chatName) {
      setNameError(true)
    } else {
      // console.log('i am OK, i am not alcoholic')
    }
  }

  const addUsers = () => {
    Request.getUserContacts()
  }

  const handleUploadImage = data => {
    const fileAccessToken = localStorage.getItem('FileAccessToken')
    axios.defaults.headers['FileAccessToken'] = fileAccessToken

    const formData = new FormData()
    formData.append('file', data.file)
  
    axios.post(`https://${props.node}:5000/api/Files`, formData)
    .then(
        (res) => {
          if (res.status === 200) {
            setLoading(false)
            setImageUrl(res.data.File.FileId)
          }
        }
    )
    .catch(
        (err) => {
          console.log(err)
        }
    )
  }

  const beforeUpload = file => {
    const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png'
    if (!isJpgOrPng) {
      message.error('Поддерживается только загрузка JPG/PNG файлов!')
    }
    const isLt2M = file.size / 1024 / 1024 < 2
    if (!isLt2M) {
      message.error('Размер изображения не должен превышать 2мб!')
    }
    return isJpgOrPng && isLt2M
  }

  const handleChange = info => {
    if (info.file.status === 'uploading') {
      setLoading(true)
      return
    }
  }

  return (
    <Modal
      visible={props.createChatModal}
      // visible={true}
      onCancel={() => props.toggleModal("createChatModal")}
      title="Создание группового чата"
      footer={null}
      maskClosable
    >
      <Upload
        listType="picture-card"
        className="avatar-uploader"
        showUploadList={false}
        customRequest={file => handleUploadImage(file)}
        beforeUpload={beforeUpload}
        onChange={handleChange}
      >
        {
          loading ? 
          <Icon type="loading" className="loading-icon" />
        : imageUrl ?
            <img src={`https://${props.node}:5000/api/Files/${imageUrl}`} alt="avatar" style={{ width: '100%' }} />
          :
            <>
              <p className="ant-upload-drag-icon">
                <Icon type="inbox" style={{fontSize: "40px", color: "#1890ff", marginTop: "15px"}} />
              </p>
              <p className="ant-upload-text">Загрузить <br/> аватар</p>
            </>
        }
      </Upload>
      <Divider />
      <Form.Item
        validateStatus={nameError ? "error" : ''}
        help={nameError ? "Это поле обязательно для заполнения" : false}
      >
        <Input 
          placeholder="Название чата"
          onChange={(e) => handleSetChatName(e.target.value)}
        />
      </Form.Item>
      <Divider />
      <div className='switch-label'>
        <div>Приватный чат</div>
        <Switch checked={chatPrivate} onChange={(value) => setChatPrivate(value)} />
      </div>
      <Divider />
      <div className='switch-label'>
        <div>Создать канал</div>
        <Switch checked={chatChannel} onChange={(value) => setChatChannel(value)} />
      </div>
      <Divider />
      <Button type="normal" block icon="usergroup-add" onClick={() => addUsers()}>Добавить пользователей</Button>
      <Divider />
      <Button type="primary" block onClick={() => handleCreateChat()}>Создать</Button>
    </Modal>
  )
}

function mapStateToProps(state) {
  return {
    createChatModal: state.ui.modals.createChatModal,
    node: state.node.Domains[0]
  }
}

const mapDispatchToProps = dispatch => {
  return {
    toggleModal: name => dispatch(toggleModal(name))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(CreateChatModal)
