import React, { useState, useEffect } from "react"
import { connect } from "react-redux"
import { ReactComponent as Paperclip } from "../assets/paperclip_icon.svg"
import { ReactComponent as SendBtn } from "../assets/send_button_icon.svg"
import { forwardingMessage } from "../actions/dialogs"
import socket from "../transport/Connect"
import { Upload, Icon, message } from "antd"
import axios from 'axios'

function NewMessageField(props) {
  const [messageText, setMessageText] = useState("")
  const [imgLoading, setImgLoading] = useState(false)
  const [imageUrl, setImageUrl] = useState(null)
  const [attachmentsType, setAttachmentsType] = useState(null)

  useEffect(() => {
    if (imageUrl) {
      sendMessage()
    }
  }, [imageUrl])

  const sendMessage = () => {
    if (props.forwardMessage) {
      // отправка пересылаемого сообщения или ответ на сообщение
      const data = {
        Messages: [
          {
            Id: 0,
            SendingTime: "",
            SenderId: props.user.Id,
            ReceiverId: props.dialog.SecondUid,
            ConversationId: props.dialog.ConversationId,
            ConversationType: props.dialog.ConversationType,
            Read: false,
            NodesId: null,
            ReplyTo: null,
            Text: messageText,
            Attachments: [
              {
                Type: 5,
                Payload: {
                  MessagesGlobalId: [props.forwardMessage.GlobalId],
                  ConversationId: props.forwardMessage.ConversationId,
                  ConversationType: props.forwardMessage.ConversationType
                }
              }
            ]
          }
        ],
        RequestType: 39,
        RequestId: socket.makeId(),
        Type: 0
      }

      socket.send(data)
    } else if (imageUrl) {
      const data = {
        Messages: [
          {
            Id: 0,
            SendingTime: "",
            SenderId: props.user.Id,
            ReceiverId: props.dialog.SecondUid,
            ConversationId: props.dialog.ConversationId,
            ConversationType: props.dialog.ConversationType,
            Read: false,
            NodesId: null,
            ReplyTo: null,
            Text: messageText,
            Attachments: [
              {
                Type: attachmentsType,
                Hash: null,
                MessageId: 0,
                Payload: imageUrl,
                NodesId: null
              }
            ],
            GlobalId: null
          }
        ],
        RequestType: 39,
        RequestId: socket.makeId(),
        Type: 0
      }

      socket.send(data)
    } else {
      // отправка обычного сообщения без вложения
      const data = {
        Messages: [
          {
            Id: 0,
            SendingTime: "",
            SenderId: props.user.Id,
            ReceiverId: props.dialog.SecondUid,
            ConversationId: props.dialog.ConversationId,
            ConversationType: props.dialog.ConversationType,
            Read: false,
            NodesId: null,
            ReplyTo: null,
            Text: messageText,
            Attachments: null,
            GlobalId: null
          }
        ],
        RequestType: 39,
        RequestId: socket.makeId(),
        Type: 0
      }

      socket.send(data)
    }

    setMessageText("")

    if(props.forwardMessage){
      props.forwardingMessage(false)
    }
  }

  const handleOnKeyDown = event => {
    if (event.keyCode === 13) {
      sendMessage()
    }
  }

  const handleUploadImage = data => {
    const fileAccessToken = localStorage.getItem('FileAccessToken')
    axios.defaults.headers['FileAccessToken'] = fileAccessToken

    const formData = new FormData()
    formData.append('file', data.file)

    axios.post(`https://${props.node}:5000/api/Files`, formData)
    .then(
        (res) => {
          if (res.status === 200) {
            setImgLoading(false)
            setImageUrl(res.data.File.FileId)
          }
        }
    )
    .catch(
        (err) => {
          console.log(err)
        }
    )
  }

  const beforeUpload = file => {
    const isJpgOrPng = file.type === "image/jpeg" || file.type === "image/png"
    if (isJpgOrPng) {
      setAttachmentsType(2)
    } else {
      setAttachmentsType(1)
    }
    return isJpgOrPng
  }

  const handleChange = info => {
    if (info.file.status === "uploading") {
      setImgLoading(true)
      return
    }
  }

  return (
    <div className="new-message">
      <Upload
        showUploadList={false}
        customRequest={file => handleUploadImage(file)}
        beforeUpload={beforeUpload}
        onChange={handleChange}
      >
        {
          imgLoading ? 
            <Icon type="loading" className="loading-icon" />
          :
            <Paperclip className="new-message__attach"/>
        }
      </Upload>
      <input
        type="text"
        className="new-message__inp"
        placeholder="Введите сообщение"
        value={messageText}
        onChange={e => {
          setMessageText(e.target.value)
        }}
        onKeyDown={handleOnKeyDown}
      />
      <SendBtn
        onClick={() => {
          sendMessage()
        }}
        className="new-message__send"
      />
    </div>
  )
}

function mapStateToProps(state) {
  return {
    user: state.users.find(user => user.currentUser === true),
    dialogs: state.dialog,
    forwardMessage: state.forwardMessage,
    node: state.node.Domains[0]
  }
}

const mapDispatchToProps = dispatch => {
  return {
    forwardingMessage: message => dispatch(forwardingMessage(message))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(NewMessageField)
