/* eslint-disable  */

function sortMessages(messages) {
  return messages.sort(function(a, b) {
    let a_date = new Date(a.SendingTime)
    let b_date = new Date(b.SendingTime)
    if (a_date < b_date) {
      return -1
    } if (a_date > b_date) {
      return 1
    } else {
      return 0
    }
  })
}

export function messages(state = [], action) {
  if (action.type === "MESSAGES_RECEIVED") {
    let newState = [...state]
    action.messages.forEach(message => {
      let existence = newState.find(mess => mess.GlobalId === message.GlobalId)
      if (!existence) {
        newState.push(message)
      }
    })

    return sortMessages(newState)
  }
  else if(action.type === "READ_MESSAGES") {
    let newState = [...state]

    newState.forEach(message => {
      if (message.GlobalId === action.messageIDs[0].GlobalId) {
        message.read = true
      }
    })

    return newState
  }
  else {
    return state
  }
}
