import React, { Component } from "react"
import { NavLink } from "react-router-dom"
import { connect } from "react-redux"
import { openDialog } from "../actions/dialogs"
import selectors from "../selectors"

class SidebarItem extends Component {
  constructor() {
    super()
    this.state = {
      defaultAva: ""
    }
  }

  componentDidMount() {
    // обрезаем строку с последним сообщением (не css потому что 2 строки)
    const size = 60
    const newsContent = this.refs.messageText
    const newsText = newsContent.innerText

    if (newsText.length > size) {
      newsContent.innerText = newsText.slice(0, size) + " ..."
    }

    // добавляем инициалы в стэйт если нет аватарки
    if (!this.props.dialog.Photo) {
      this.setState({
        defaultAva: selectors.getUserInitials(this.props.dialog)
      })
    }
  }

  openDialog() {
    const payload = {}
    payload.type = this.props.type
    payload.data = this.props.dialog
    this.props.openDialog(payload)
  }

  render() {
    const avatarStyle = {
      width: "100%",
      height: "100%",
      backgroundSize: "cover",
      backgroundPosition: "center center",
      backgroundImage: `url(https://${this.props.node}:5000/api/Files/${this.props.dialog.Photo})`,
      backgroundRepeat: "no-repeat"
    }

    // const defaultAvaBg = {
    //   background: `hsl(${this.props.dialog.SecondUid}, 90%, 90%)`
    // }

    return (
      <NavLink to={"/"} onClick={() => this.openDialog()}>
        <div className="sidebar__item">
          <div className="sidebar__ava">
            {this.props.dialog.UnreadedCount > 0 ? (
              <div className="sidebar__badge">
                {this.props.dialog.UnreadedCount}
              </div>
            ) : null}
            <div className="sidebar__photo">
              {this.props.dialog.Photo ? (
                <div style={avatarStyle} />
              ) : (
                <div className="sidebar__photo-default avatar-default">
                  {this.state.defaultAva}
                </div>
              )}
            </div>
          </div>
          <div>
            <div className="sidebar__name">
              <span>{this.props.dialog.Title || this.props.dialog.NameFirst + this.props.dialog.NameSecond || this.props.dialog.ChannelName || this.props.dialog.Name || 'Имя скрыто'}</span>
            </div>
            <div className="sidebar__message" ref="messageText">
              {
                this.props.dialog.PreviewText ? this.props.dialog.PreviewText :
                this.props.dialog.AttachmentType ===  1 ? 'Файл' :
                this.props.dialog.AttachmentType ===  2 ? 'Изображение' :
                this.props.dialog.About ? this.props.dialog.About : null
              }
            </div>
          </div>
        </div>
      </NavLink>
    )
  }
}

function mapStateToProps(state) {
  return {
    node: state.node.Domains[0]
  }
}

const mapDispatchToProps = dispatch => {
  return {
    openDialog: dialogId => dispatch(openDialog(dialogId))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(SidebarItem)
