export function dialogsReceived(dialogs) {
  return {
    type: "DIALOGS_RECEIVED",
    dialogs
  }
}

export function chatsReceived(chats) {
  return {
    type: "CHATS_RECEIVED",
    chats
  }
}

export function messagesReceived(messages) {
  return {
    type: "MESSAGES_RECEIVED",
    messages
  }
}

export function readMessages(messageIDs) {
  return {
    type: "READ_MESSAGES",
    messageIDs
  }
}

export function forwardingMessage(message) {
  return {
    type: "FORWARD_MESSAGE",
    message
  }
}

export function openDialog(payload) {
  return {
    type: "OPEN_DIALOG",
    payload
  }
}

export function closeDialog() {
  return {
    type: "CLOSE_DIALOG"
  }
}
