/* eslint-disable */
class Connect {
  constructor() {
    
  }

  start(domain){
    this.webSocket = new WebSocket(`wss://${domain}:5000`)

    this.webSocket.onopen = () => {
      console.log("Соединение установлено")
      this.wsOnConnect()
    }

    this.webSocket.onclose = event => {
      if (event.wasClean) {
        console.log("Соединение закрыто")
      } else {
        console.log("Обрыв соединения")
      }
      console.log("Код: ", event)
    }

    this.webSocket.onmessage = event => {
      const message = this.blobToJson(event.data)

      if (message.ErrorCode && message.ErrorCode !== 0) {
        console.log('%c REQUEST ERR ',  'background: #ff4545; color: #bada55; padding: 3px', message)
      } else {
        this.onMessage(message)
        this.onMessageAuth(message)
        console.log(
          "%c RESPONSE ",
          "background: #00ff00; color: #000; padding: 3px",
          this.blobToJson(event.data)
        )
      }
    }

    this.webSocket.onerror = error => {
      console.log('%c ERROR CONNECT ',  'background: red; color: #bada55; padding: 3px', error);
    }
  }

  getStatus(){
    return this.webSocket.readyState
  }

  blobToJson(data) {
    var u, x
    u = URL.createObjectURL(data)
    x = new XMLHttpRequest()
    x.open("GET", u, false)
    x.send()
    URL.revokeObjectURL(u)

    return JSON.parse(x.responseText)
  }

  makeId() {
    var result = ""
    var characters = "0123456789"
    var charactersLength = characters.length
    for (var i = 0; i < 18; i++) {
      result += characters.charAt(Math.floor(Math.random() * charactersLength))
    }

    return parseInt(result)
  }

  send(data) {
    console.log(
      "%c REQUEST ",
      "background: #222; color: #bada55; padding: 3px",
      data
    )
    this.webSocket.send(JSON.stringify(data))
  }

  onMessage() {}

  onMessageAuth() {}

  wsOnConnect() {}
}

const wSocket = new Connect()

export default wSocket
