/* eslint-disable */
export function searchResult(state = null, action) {
  if(action.type === 'SEARCH_RESULT_RECEIVED') {
    let newState = {...state}
    newState = action.searchResult
    return newState
  }
  else if(action.type === 'SEARCH_END') {
    let newState = {...state}
    newState = null
    return newState
  }
  else {
    return state
  }
}
