/* eslint-disable */
export function users(state = [], action) {
  if(action.type === 'USER_RECEIVED') {
    let newState = state;
    
    if(action.users.length > 0) {
      let foundUser = newState.find(user => user.Id === action.users[0].Id);

      if (!foundUser) {
        newState = [...newState, ...action.users];
      }
    }
    
    return newState;
  } else {
    return state;
  }
}
