/* eslint-disable */
import React, { useState, useEffect } from "react"
import { connect } from "react-redux"
import SidebarItem from "./SidebarItem"
import HeadSidebar from "./HeadSidebar"
import Search from "./Search"
import { openDialog } from "../actions/dialogs"
import socket from "../transport/Connect"

function Sidebar(props) {
  const [localFoundDialogs, setLocalFoundDialogs] = useState([])

  useEffect(() => {
    if (props.search) {
      const searchDialogs = props.dialogs.filter(dialog => {
        if (!dialog.fake) {
          if (dialog.Title) {
            return dialog.Title.toLowerCase().indexOf(props.search) >= 0
          }
        }
      })
      setLocalFoundDialogs(searchDialogs)
    } else {
      setLocalFoundDialogs(props.dialogs)
    }
  }, [props.search, props.dialogs])

  const showSidebarItem = () => {
    return localFoundDialogs.map(dialog => {
      if(dialog && !dialog.fake){
        return <SidebarItem dialog={dialog} key={socket.makeId()} type='dialog' />
      }
    })
  }

  const showSearchResultChannels = () => {
    if (props.searchResult.channels) {
      return props.searchResult.channels.map((dialog, i) => {
        if(dialog){
          if (i === 0) {
            return (
              <div key={socket.makeId()}>
                <div className="sidebar__title">Каналы:</div>
                <SidebarItem dialog={dialog} type="channel" />
              </div>
            )
          } else {
            return (
              <SidebarItem dialog={dialog} type="channel" key={socket.makeId()} />
            )
          }
        }
      })
    }
  }

  const showSearchResultChats = () => {
    if (props.searchResult.chats) {
      return props.searchResult.chats.map((dialog, i) => {
        if(dialog){
          if (i === 0) {
            return (
              <div key={socket.makeId()}>
                <div className="sidebar__title">Чаты:</div>
                <SidebarItem dialog={dialog} type="chat" />
              </div>
            )
          } else {
            return (
              <SidebarItem dialog={dialog} type="chat" key={socket.makeId()} />
            )
          }
        }
      })
    }
  }

  const showSearchResultUsers = () => {
    if (props.searchResult.users) {
      return props.searchResult.users.map((dialog, i) => {
        if(dialog){
          if (i === 0) {
            return (
              <div key={socket.makeId()}>
                <div className="sidebar__title">Пользователи:</div>
                <SidebarItem dialog={dialog} type="user" />
              </div>
            )
          } else {
            return (
              <SidebarItem dialog={dialog} type="user" key={socket.makeId()} />
            )
          }
        }
      })
    }
  }

  return (
    <div className="sidebar">
      <HeadSidebar />
      <Search />
      <div className="sidebar-viewport">
        {showSidebarItem()}
        {props.searchResult ? showSearchResultUsers() : null}
        {props.searchResult ? showSearchResultChats() : null}
        {props.searchResult ? showSearchResultChannels() : null}
      </div>
    </div>
  )
}

function mapStateToProps(state) {
  return {
    dialogs: state.dialogs,
    search: state.ui.search,
    searchResult: state.searchResult
  }
}

const mapDispatchToProps = dispatch => {
  return {
    openDialog: dialogId => dispatch(openDialog(dialogId))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Sidebar)
